.slider {

    display: flex;
    flex-direction: column;
    align-items: center;

    &__box {
        display: flex;
        margin-bottom: 30px;
    }

    &__circle {
        width: 66px;
        height: 66px;
        @include text(30px, 700, 44px, $primaryColor);
        margin-bottom: 20px;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        @include flex (center, center);
        background-color: $accentColor;
        border-radius: 50%;
    }

    &__text {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
    }

    &__text-one {
        @include text(16px, 500, 23px, $textColor);
        margin-bottom: 10px;
    }

    &__text-two {
        @include text(14px, 400, 20px, $textColor);
        text-align: center;
    }
}